import React from 'react';
import { connect } from 'react-redux';
import { fetchPreviewData } from '../../../storage/thunkActions'
import ButtonActionsComponent from './ButtonActionsComponent';


class ActionsComponent extends React.Component {
  state = {
      data: ''
  }
  
  async getText() {
      
  }

  async componentDidMount() {
    this.setState({ data: this.props.data });
  }

   render() {
      const { blName, data, /*guid_list_view*/guid,dispatchFetchPreviewData,can_edit_list_view,can_delete_list_view, can_detail_list_view,action_icons} = this.props;
      //let dotnethelperListView = window.dotnethelpersListView.get(guid_list_view);
      let dotnethelperListView = window.dotnethelpersListView.get(guid);

      const onEdit = async () => {        
        /*let url = blName + '/edit/' + data.rowid;
        window.SDKDotNetHelper.invokeMethodAsync("NavigateTo", url);*/
        await dotnethelperListView.invokeMethodAsync("EditFromReact", data.rowid);
      }
      const onDetail = async () => {
        await dotnethelperListView.invokeMethodAsync("DetailFromReact", data.rowid);
        /*let url = blName + '/detail/' + data.rowid;
        window.SDKDotNetHelper.invokeMethodAsync("NavigateTo", url);*/
      }
      const onDelete = async () => {
        let dataStr = JSON.stringify(data);
        //let dotnethelperListView = window.dotnethelpersListView.get(guid_list_view);
        let response =  await dotnethelperListView.invokeMethodAsync("DeleteFromReact", data.rowid, dataStr);
        if (response) {
          dispatchFetchPreviewData(); 
        }
      }
      
      return (
        <div className="action-buttons d-flex justify-content-center">
          {can_edit_list_view ? <button className="btn btn-sm layout_btn_standar-sm ml-1" onClick={onEdit} ><i className="fa-solid fa-pen"></i></button> : <></>}
          {can_delete_list_view ? <button className="btn btn-sm layout_btn_standar-sm ml-1" onClick={onDelete} ><i className="fa-solid fa-trash-can"></i></button> : <></>}
          {can_detail_list_view ? <button className="btn btn-sm layout_btn_standar-sm ml-1" onClick={onDetail} ><i className="fa-solid fa-eye"></i></button> : <></>}          
          {action_icons.map((element, index) => {
            let disabled = element.disabled;
            let hide = element.hidden;
            let icon = element.icon;
            return (
              <ButtonActionsComponent key={icon+"_"+index+"_"+data.rowid} icon={icon} data={data} index={index} guid={guid} disabled={disabled} hide={hide}/>
            )
          })}
        </div>
      );
  }
}

const mapStateToProps = state => {
  return {
    'blName': state.general.bl_name_list,
    'guid': state.general.guid,
    //'guid_list_view': state.general.guid_list_view,
    'can_edit_list_view': state.general.can_edit_list_view,
    'can_delete_list_view': state.general.can_delete_list_view,
    'can_detail_list_view': state.general.can_detail_list_view,
    'action_icons': state.general.action_icons,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    dispatchFetchPreviewData: () => dispatch(fetchPreviewData())
  }
}

export default connect(mapStateToProps,mapDispatchToProps)(ActionsComponent)
