import React, { Component } from 'react';
import Select from 'react-select';
import { connect } from 'react-redux'
import { setIsOpenModalFormula, setModeModalFormula, setIndexModalFormula, setEditModeDataModalFormula } from '../../../storage/actions'
import { Form, FormGroup, Input, Label, Badge } from 'reactstrap';
import {UtilManager} from '../../../Utils';
import ResourceViewer from '../../Visualization/ResourceViewer';

import cloneDeep from 'lodash/cloneDeep'

import {
	emptyDesciptionObj
} from '../../../constants'


// const Label_Count =  UtilManager.GetResource("BLFlex.Label.Count");
// const Label_Sum =  UtilManager.GetResource("BLFlex.Label.Sum");
// const Label_Average =  UtilManager.GetResource("BLFlex.Label.Average");
// const Label_Min =  UtilManager.GetResource("BLFlex.Label.Min");
// const Label_Max =  UtilManager.GetResource("BLFlex.Label.Max");

const column_options = [
    { value: "count", label: "BLFlex.Label.Count" },
    { value: "sum", label: "BLFlex.Label.Sum"},
    { value: "avg", label: "BLFlex.Label.Average" },
    { value: "max", label: "BLFlex.Label.Min" },
    { value: "min", label: "BLFlex.Label.Max" }
]

function GetResourceColumOption(options) {
    const ColumnOptions = [];
  
    for (const option of options) {
      const _resourceLabel = UtilManager._getResource(option.label);
      ColumnOptions.push({ value: option.value, label: _resourceLabel });
    }
    return ColumnOptions;
  }


export class FieldOptions extends Component {
    render() {
        const { onHandleChange, field, isCreateEdit } = this.props;
        return (<Form>
            <FormGroup>
                <Label for="module"><ResourceViewer resourceTag="BLFlex.Label.Module" /></Label>
                <Input className="input-sm" bsSize="sm" type="text" name="module" disabled value={field.path} />
            </FormGroup>
            <FormGroup>
                <Label for="name"><ResourceViewer resourceTag="BLFlex.Label.Field" /></Label>
                <Input className="input-sm" bsSize="sm" type="text" name="name" disabled value={field.name} />
            </FormGroup>
            { field.customFn ? <FormGroup>
                <Label for="name"><ResourceViewer resourceTag="BLFlex.Label.Formula" /></Label>{isCreateEdit ? <Badge color="secondary" className="edit-function-bagged-popover" onClick={ async (e) => {
                    this.props.dispatchSetModeModalFormula('edit')
                    this.props.dispatchSetIndexModalFormula(this.props.index)
                    this.props.dispatchSetEditModeDataModalFormula({
                        label: field.label,
                        name: field.name,
                        formula: field.formula,
                        description: cloneDeep(emptyDesciptionObj),
                        section: 'columns',
                    })
                    document.getElementsByClassName("popover show bs-popover-auto")[0].parentNode.classList.toggle("hidden")
                    this.props.dispatchSetIsOpen(true)
                } }><i className="fa fa-edit" aria-hidden="true"></i></Badge> : null }
                <Input className="input-sm" bsSize="sm" type="text" name="name" disabled value={field.formula} />
            </FormGroup> : null }
            <FormGroup>
                <Label for="label"><ResourceViewer resourceTag="BLFlex.Label.Tag" /></Label>
                <Input disabled={!isCreateEdit} className="input-sm" bsSize="sm" type="text" name="label" onChange={(e) => { onHandleChange("label", e.target.value); }} value={field.label} />
            </FormGroup>
            <FormGroup>
                <Label for="aggregate"><ResourceViewer resourceTag="BLFlex.Label.Operation" /></Label>
                <Select 
                    isDisabled={!isCreateEdit} 
                    placeholder={<ResourceViewer resourceTag="BLFlex.PlaceHolder.Operation" />} 
                    options={GetResourceColumOption(column_options)} 
                    onChange={(e) => { onHandleChange("aggregate", e.value); }} 
                    value={column_options.find((e) => { return e.value === field.aggregate })} 
                />
            </FormGroup>
            <FormGroup check inline>
                <Label check>
                    <ResourceViewer resourceTag="BLFlex.Label.Hide" /> {' '}                    
                    <Input disabled={!isCreateEdit} type="checkbox" name="hide" onChange={(e) => { onHandleChange("hide", (e.target.checked ? 'True' : 'False'));}} checked={((field.hide && typeof field.hide === "boolean") || field.hide === 'True') ? true : false} />                    
                </Label>
            </FormGroup>
            {/* <FormGroup check inline>
                <Label check>
                    <ResourceViewer resourceTag="BLFlex.Label.Link" /> {' '}
                    <Input disabled={!isCreateEdit} type="checkbox" name="link" onChange={(e) => { onHandleChange("link", (e.target.checked ? 'True' : 'False')); }} checked={((field.link && typeof field.link === "boolean") || field.link === 'True') ? true : false} />
                </Label>                
            </FormGroup> */}
            {/* <FormGroup check inline>
                <Label check>
                    Totalizar {' '}
                    <Input disabled={!isCreateEdit} type="checkbox" name="footer_total" onChange={(e) => { onHandleChange("footer_total", (e.target.checked ? 'True' : 'False')); }} checked={((field.footer_total && typeof field.footer_total === "boolean") || field.footer_total === 'True') ? true : false} />
                </Label>
            </FormGroup> */}
        </Form>);
    }
}

const mapDispatchToProps = dispatch => {
	return {
        dispatchSetIsOpen: isOpen => dispatch(setIsOpenModalFormula(isOpen)),
        dispatchSetModeModalFormula: mode => dispatch(setModeModalFormula(mode)),
        dispatchSetIndexModalFormula: index => dispatch(setIndexModalFormula(index)),
        dispatchSetEditModeDataModalFormula: data => dispatch(setEditModeDataModalFormula(data))
	}
}

export default connect(undefined, mapDispatchToProps)(FieldOptions);