import cloneDeep from 'lodash/cloneDeep';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import FieldsContainer from './FieldsContainer';
import FormulaModal from '../FormulaModal';
import { setIsOpenModalFormula, setModeModalFormula } from '../../storage/actions'
import ResourceViewer from '../Visualization/ResourceViewer';

export class Sidebar extends Component {
  constructor(props) {
    super(props);
    this.state = {
      current_tab: 0,
      collapse: (props.is_create_edit ? false : true),
      change_tab: false
    }
  }

  changeTab(index) {
    this.setState({ current_tab: index });
  }

  onClickIcon() {
    let new_index = Math.abs(this.state.current_tab - 1);
    this.setState({ current_tab: new_index, change_tab: true });
  }

  toggleCollapse = () => {
    this.setState(prevState => ({
      collapse: !prevState.collapse
    }));
  }

  openEditFormula = () => {
    this.props.dispatchSetModeModalFormula('add')
    this.props.dispatchSetIsOpen(true)
  }

  filterFieldList = (excluded_keys) => {
    let result = [];
    this.props.field_list.forEach(element => {
      let tmp = cloneDeep(element);
      if (tmp.options) {
        let new_options = [];
        for (let index = 0; index < tmp.options.length; index++) {
          const option = tmp.options[index];
          if (excluded_keys.indexOf(option.key_name) === -1) {
            new_options.push(option);
          }
        }
        tmp.options = new_options;
        result.push(tmp);
      }
    });
    return result;
  }

  render() {
    const { columns, groups, filters, is_create_edit, is_list_view } = this.props;
    // this.state.current_tab = (is_list_view && !this.state.change_tab) ? 0 : this.state.current_tab;
    // this.state.change_tab = false;
    let added_fields = columns.map(field => { return field.key_name }).concat(groups.map(field => { return field.key_name }));
    if (this.state.collapse) {
      return (
        <div className="oreports-sidebar collapsed d-flex align-items-center" onClick={this.toggleCollapse}>
          <div className="sidebar-header">            
            <div className="sidebar-icon" ><i className="fa fa-chevron-right" aria-hidden="true"></i></div>
          </div>
          <div class="table-edit-title">Edicin de Tabla</div>
        </div>
      );
    }
    return (
      <div className="oreports-sidebar">
        <div className="sidebar-header pl-3 pr-3">
          {this.state.current_tab === (is_create_edit ? 0 : 1) ? <div className="sidebar-title"><i className="fa-solid fa-table" aria-hidden="true"></i><ResourceViewer resourceTag="BLFlex.Label.BaseTable" /></div> : null} 
          <div className="sidebar-icon" id="sidebar-icon-filter" onClick={() => { this.onClickIcon(); }}>{this.state.current_tab === (is_create_edit ? 0 : 1) ? <i className="fa-solid fa-filter" aria-hidden="true"></i> : <i className="fa fa-th" aria-hidden="true"></i>}</div>
          {this.state.current_tab === (is_create_edit ? 1 : 0) ? <div className="sidebar-title"><i className="fa-solid fa-filter" aria-hidden="true"></i>
            <ResourceViewer resourceTag="BLFlex.Label.Filters" /></div> : null}
        </div>
        <hr class="table-separator"></hr>
        <div className="pl-3 pr-3 pb-3">
          {this.state.current_tab === (is_create_edit ? 0 : 1) ? <>
            <FormulaModal /> 
            
            <FieldsContainer title="Columnas" section="columns" fields={columns} field_list={this.filterFieldList(added_fields)}
              titleButton={(is_create_edit ? { classes: 'btn btn-default oreports-primary-button outlined custom-columns-function', click: this.openEditFormula, classIcon: 'fa-solid fa-sigma' } : null)} />
              
            <hr></hr> 
            <FieldsContainer title="Grupos" section="groups" fields={groups} field_list={this.filterFieldList(added_fields)} />
            
          </> : <FieldsContainer title="Filtros" notitle={true} section="filters" fields={filters} field_list={this.filterFieldList([])} />}
        </div>
        <div class="sidebar-extra" onClick={this.toggleCollapse}>
        <div className="sidebar-collapse d-flex flex-column align-items-center">
          <div class="sidebar-header"><i className="fa fa-chevron-left" aria-hidden="true"></i></div>
          <div class="table-edit-title">Edicin de Tabla</div>
          </div>
        </div>
      </div>
     
    );
  }
}

const mapStateToProps = state => {
  return {
    'field_list': state.general.field_list,
    'columns': state.general.columns,
    'groups': state.general.groups,
    'filters': state.general.filters,
    'is_create_edit': state.general.is_create_edit,
    'is_list_view': state.general.is_list_view,
  };
};

const mapDispatchToProps = dispatch => {
  return {
    dispatchSetIsOpen: isOpen => dispatch(setIsOpenModalFormula(isOpen)),
    dispatchSetModeModalFormula: mode => dispatch(setModeModalFormula(mode))
  }
}


export default connect(mapStateToProps, mapDispatchToProps)(Sidebar);
