import React from "react";
import { connect } from 'react-redux';
import {updatePreviewData} from '../../../storage/actions';
import {fetchData} from '../../../storage/thunkActions';
import {store} from '../../../index';
import { UtilManager } from "../../../Utils"

class ButtonActionsComponent extends React.Component {
    state = {
        disabled: this.props.disabled,
        hide: this.props.hide,
        dataMap: {}
    }

    constructor(props) {
        super(props);
        this.onCustomAction = this.onCustomAction.bind(this);
        this.initButton = this.initButton.bind(this);
        this.initButton();
    }

    async onCustomAction (){
        const {index, guid} = this.props;

        const dotnethelperListView = window.dotnethelpersListView.get(guid);

        dotnethelperListView.invokeMethodAsync("CustomActionFromReact", index, this.state.dataMap.rowid).then((response) => {
            if(response){
                let current_state = store.getState().general;
                let url = '/api/BLFlex/PreviewData/'
                let params = {};
                const regularColumns = this.props.columns.filter(col => !col.customFn);
                params = {
                    "module_path": current_state.report_header_data.module_path,
                    "selected_class": current_state.report_header_data.selected_class,
                    "module": current_state.report_header_data.module,
                    "filters": current_state.filters,
                    "columns": regularColumns,
                    "groups": current_state.groups,
                    "set_top": 'false',
                    "filter_search": "Rowid == "+this.props.data.rowid,
                }
                let fetchParams ={
                    method: 'POST',
                    headers: {
                      "Accept": "application/json",
                      'X-Requested-With': 'XMLHttpRequest'
                    },
                    body: JSON.stringify(params)
                }
                
                fetchData(url, fetchParams, current_state, null, true).then((data) => {
                    this.props.updatePreviewData(data);                        
                });
            }
        });
    }

    async initButton (){
        const {data, columns, index, guid} = this.props;
        const dotnethelperListView = window.dotnethelpersListView.get(guid);
        let dataMap = await UtilManager.MapData(data, columns)
        this.setState({dataMap: dataMap});

        dotnethelperListView.invokeMethodAsync("DisableActionFromReact", JSON.stringify(dataMap), index).then((response) => {
            this.setState({ disabled: response });
        });        
        dotnethelperListView.invokeMethodAsync("HideActionFromReact", JSON.stringify(dataMap), index).then((response) => {
            this.setState({ hide: response });
        });

    }
    
    render() {
        const { icon} = this.props;

        return (
            <div>
            {!this.state.hide && <button className="btn btn-sm layout_btn_standar-sm ml-1" onClick={() => {this.onCustomAction()}}  disabled={this.state.disabled}>  
                <i className={icon}></i>
              </button>}
            </div>
        );
    }
}

const mapStateToProps = state => {
    return {      
      'columns': state.general.columns,
      'column_rowid': state.general.column_rowid
    };
  };

const mapDispatchToProps = dispatch => {
return {
    'updatePreviewData': async (data) => {
        await dispatch(updatePreviewData(data));
        dispatch(updatePreviewData());
    }
}  
};

export default connect(mapStateToProps, mapDispatchToProps)(ButtonActionsComponent)